import React from 'react'
import Lottie from 'react-lottie';
import Animacion_Gracias from '../../Animacion/93494-step-loader.json';



const defaultOptions = {
  loop: true,
  autoplay: true,
  rendererSetting: {
    preserveAspectRatio: 'xMidYMid slice'
  }


};


const Contenido_Proyecto_Presentacion = () => {
  return (
    <div className="container-fluid">
      <div className="Fondo_Proyecto_Presentacion">
        <Lottie options={{ animationData: Animacion_Gracias, defaultOptions }} />
      </div>
    </div>
  )
}

export default Contenido_Proyecto_Presentacion